import React from "react";


const BusRegistration = (props) => {
    const { currentLanguage, open } = props;
    console.log(props.open);
    const Submit = (e) => {
        const formEle = document.querySelector("form")
        e.preventDefault()     
        console.log("submitted")
        const formDatab = new FormData(formEle)
        fetch(
            "https://script.google.com/macros/s/AKfycbws7QeS8VkNOkdCOngRqQKkZGhsBiiYx6hQ74qV6B0B8J-8QoRfrHzTeijfuDhQEGTm/exec",
            {
                // redirect: "follow",
                method: "POST",
                body: JSON.stringify(formDatab),
                headers: {
                    'Content-Type': 'text/plain;charset=utf-8',
                }
            }
          )
            .then((res) => res.json())
            .then((data) => {
                
                console.log(data);            
            })
            .catch((error) => {
              console.log(error);
            });
        }
    return (
        <>
        {open && (
            <section className="event" id="registration">
                {currentLanguage === "hu" ? (
                <div className="box boxHu" id="reg">
                    <h1>BUSZ REGISZTRÁCIÓ</h1>
                    <form className="form" onSubmit={(e)=>Submit(e)}>
                        <label>Tulajdonos neve
                            <input placeholder="Tulajdonos neve" name="Name" type="text"/>
                        </label>
                        <p>Elérhetőség</p>
                        <label>E-mail címe
                            <input placeholder="E-mail" name="Email" type="Email"/>
                        </label>
                        <label>Ikarus busz típusa
                            <input placeholder="Ikarus busz típusa" name="Bus" type="text"/>
                        </label>
                        <label>Fotó feltöltése(1db)
                            <input type="text" />
                        </label>
                        <input className="button" type="submit"
                        />
                    </form>
                </div>
                ) : (
                <div className="box boxEn" id="reg">
                    <h1>BUS REGISTRATION</h1>
                </div>
                )}
            </section>
        )}
      </>
    );
  };
  
  export default BusRegistration;
  